html {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box; }

/* components */
.CvBuilder {
  display: flex;
  height: 100vh; }
  .CvBuilder .Divider {
    height: 1px;
    width: 95%;
    border: none;
    background-color: rgba(0, 0, 0, 0.1);
    margin: 30px 0 30px 0; }

.SideBar {
  flex: 0 0 300px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: auto;
  padding-bottom: 40px;
  /* Tools */ }
  .SideBar .DownloadButtons .buttonBlue, .SideBar .DownloadButtons .buttonRed {
    color: #fff;
    margin: 0 10px 0 10px; }
  .SideBar .DownloadButtons .buttonBlue {
    background-color: #03a9f4; }
  .SideBar .DownloadButtons .buttonRed {
    background-color: #f44336; }
  .SideBar .OptionSelector .formControl {
    min-width: 240px; }
  .SideBar .FieldToggle {
    min-width: 240px;
    min-height: 60px;
    display: flex;
    align-items: center; }
    .SideBar .FieldToggle label {
      width: 100%;
      height: 100%;
      margin: 0;
      justify-content: space-between;
      padding-left: 15px; }
    .SideBar .FieldToggle span:last-child {
      font-size: 20px; }
  .SideBar .PersonalInfoToggle {
    margin: 0 0 20px; }
    .SideBar .PersonalInfoToggle .header {
      height: 50px;
      min-height: 50px; }
    .SideBar .PersonalInfoToggle .container {
      display: flex;
      flex-direction: column; }
      .SideBar .PersonalInfoToggle .container .FieldToggle {
        min-width: 160px;
        min-height: 20px;
        display: flex;
        align-items: center;
        margin-bottom: 5px; }
        .SideBar .PersonalInfoToggle .container .FieldToggle label {
          width: 100%;
          height: 100%;
          margin: 0;
          justify-content: space-between;
          padding-left: 10px; }
        .SideBar .PersonalInfoToggle .container .FieldToggle span:last-child {
          font-size: 12px; }

.BuilderBody {
  flex: 1;
  background-color: #f7f7f7;
  display: flex;
  align-items: center;
  justify-content: center; }
  .BuilderBody .cvBody {
    width: 595px;
    height: 842px;
    background-color: #fff;
    display: flex;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2); }
    .BuilderBody .cvBody .cvLeftContent {
      flex: 0 0 39%;
      display: flex;
      flex-flow: column; }
      .BuilderBody .cvBody .cvLeftContent .cvLeftDraggable {
        background-color: #f4f4f4;
        padding: 10px;
        box-sizing: border-box;
        flex-grow: 1;
        overflow: hidden; }
    .BuilderBody .cvBody .cvRightContent {
      flex: 0 0 61%;
      display: flex;
      flex-flow: column; }
      .BuilderBody .cvBody .cvRightContent .cvRightDraggable {
        padding: 10px;
        box-sizing: border-box;
        flex-grow: 1;
        overflow: hidden; }

.DefaultField {
  font-style: italic; }
  .DefaultField .fieldHeader {
    height: 20px;
    text-transform: uppercase;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #333;
    color: white;
    cursor: grab; }
  .DefaultField .fieldContent {
    padding-top: 8px;
    font-size: 11px; }
    .DefaultField .fieldContent .DetailedListElement, .DefaultField .fieldContent .BasicListElement {
      padding-bottom: 8px; }
      .DefaultField .fieldContent .DetailedListElement textarea, .DefaultField .fieldContent .BasicListElement textarea {
        background-color: inherit;
        font-style: italic;
        padding: 0;
        margin: 0;
        border: none;
        width: 100%;
        resize: none; }
      .DefaultField .fieldContent .DetailedListElement .titleInput, .DefaultField .fieldContent .BasicListElement .titleInput {
        font-weight: bold; }
    .DefaultField .fieldContent .BasicListElement {
      display: flex; }
      .DefaultField .fieldContent .BasicListElement textarea {
        display: inline-block;
        flex: 1; }
      .DefaultField .fieldContent .BasicListElement textarea:first-child {
        font-weight: bold;
        margin-right: 20px;
        text-align: right; }

.ghost {
  opacity: 0.5; }

.ImageField img {
  width: 100%;
  height: 100%; }

.HeaderField {
  font-size: 11px;
  background-color: #333;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px; }
  .HeaderField h1 {
    margin: 8px; }
  .HeaderField textarea {
    background-color: transparent;
    color: white;
    font-style: italic;
    padding: 0;
    border: none;
    width: 100%;
    resize: none;
    text-align: center;
    margin-bottom: 8px; }
  .HeaderField textarea:last-child {
    margin: 0; }

.PersonalInfoField {
  padding: 12px 10px 0;
  font-size: 11px;
  background-color: #333;
  color: white; }
  .PersonalInfoField label {
    font-size: 10px;
    text-transform: uppercase;
    font-weight: bold; }
  .PersonalInfoField textarea {
    font-style: italic;
    margin-bottom: 8px;
    background-color: transparent;
    color: white;
    padding: 0;
    border: none;
    width: 100%;
    resize: none; }
  .PersonalInfoField .noMargin {
    margin-bottom: 0; }
